import React from 'react'; 

function Calculator() {
  const buttons = ['AC', '()', '%', '/', '7', '8', '9', 'x', '4', '5', '6', '-', '1', '2', '3', '+', '0', '.', '='];
  let [expression, setExpression] = React.useState('0');
  const [result, setResult] = React.useState('0');
  const pattern = /^[0-9+-/\*\.()%]*$/;
  const errorPattern = /[+-/\*\.%]{2}/;

  function onClickButton(e) {
    const lastSymbol = expression[expression.length-1];
    const newSymbol = e.target.textContent;

    switch (newSymbol) {
      case '=':
        expression = eval(expression);
        break;
      case 'AC':
        expression = '0';
        break;
      case '()':
        if(isNaN(+lastSymbol)) {
          expression += '(';
        } else {
          expression += ')';
        }
        break;
      case 'x':
        expression += '*';
        break;
      default:
        expression += newSymbol;
    }

    if (expression.length === 2 && expression[1] !== ',') {
      expression = expression.replace(/^0/,'');
    }
    
    if (!errorPattern.test(expression)) {
      setExpression(expression);
      if (newSymbol !== '=') {
        setResult(expression);
      }
    }
  }

  return (
    <section className='calc'>
      <span className='calc__math-expression'>{result}</span>
      <input 
        className='calc__input' 
        type='text' 
        name='expression' 
        value={expression} 
        maxLength="50" 
        autoFocus
      />
      <div className="calc__buttons">
      {buttons.map((button) => (
        <button key={buttons.indexOf(button)} onClick={onClickButton} className={
          button === 'AC' || button === '()' || button === '%'? 'calc__button calc__button_white': 
          '%x-+=/'.includes(button) ? 'calc__button calc__button_orange': 
          button === '0'? 'calc__button calc__button_black calc__button_black_0': 'calc__button calc__button_black'
        }>{button}</button>
      ))}
      </div>
    </section>
  );
}

export default Calculator;